<template>
  <div id="background">
      <div id="body">
            <div class="container">
                <label for="">Title</label>
                <input type="text" name="" id="">
            </div>
            
            <div>
                <label for="">Upload File</label>
                <input type="file" @change="upload" name="" id="">  
            </div> 

            <div class="container">
                <label for="">Link:</label>
                <input type="text" name="" id="">
            </div>
            
            <div>
                <label for="">Text</label><br>
                <textarea name="" id="" cols="30" rows="10"></textarea>
            </div>

            <button>Send</button>
      </div>
  </div>
</template>

<script>
import axios from "axios"
export default {
    name: "projectForm",
    data(){
        return{
            file: ""
        }
    },
    methods: {
        async upload(e){
            let form = new FormData()
            form.append("file", e.currentTarget.files[0])
            await axios.post("/api/gallery/upload", form).then((res) => {
                console.log(res)
            }).catch((err) => {
                console.log(err)
            })
        }
    }
}
</script>

<style scoped>
    #background{
        flex: 1 1 auto;
        min-height: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        /* height: 100%; */
    }
    #body{
        background-color: coral;
        display: flex;
        flex-flow: column;
        justify-content: center;
        align-items: center;
        row-gap: 5px;
    }
    .container{
        width: fit-content;
        background-color: #ccc;
    }
    .container > label{
        height: 100%;
        padding: 5px;
    }
    .container > input{
        padding: 5px;
        background-color: #fff;
    }
</style>