<template>
  <div id="gallery">
      <nav>
          <router-link to="/" id="title">Logan Reuter</router-link>
          <router-link to="/gallery/add">Add Project</router-link>
          <router-link to="/login" v-if="loggedIn == false">Log In</router-link>
          <a @click="logOut" v-else>Log Out</a>
      </nav>
      <router-view/>
  </div>
</template>

<script>
export default {
    name: "Gallery",
    data(){
        return{
            loggedIn: false
        }
    },
    mounted(){
        let loggedIn = window.sessionStorage.getItem("loggedIn")
        if(loggedIn != null && loggedIn != "false"){
            this.loggedIn = true
        } else {
            this.loggedIn = false
        }
    },
    methods: {
        logOut(){
            window.sessionStorage.setItem("loggedIn", "false")
            this.loggedIn = false
        }
    }
}
</script>

<style scoped>
    #gallery{
        display: flex;
        flex-flow: column;
        height: 100vh;
    }
    nav{
        padding: 10px;
        background-color: aqua;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        column-gap: 20px;
    }
    a:hover{
        cursor: pointer;
    }
    #title{
        margin-right: auto;
    }
</style>