<template>
  <div id="gallery">
      <h1>Gallery Preview</h1> 
      <p>Here is a preview of some of my most recent projects.</p> 
      <router-link to="/gallery" id="btn">See Full Gallery</router-link>
      <div id="prev-container">
            <button id="left" v-if="focusedTile > 0" @click="shiftLeft"> 
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M16.67 0l2.83 2.829-9.339 9.175 9.339 9.167-2.83 2.829-12.17-11.996z"/></svg>
            </button>
            <div id="preview">
                <tile v-for="(file, index) in files" :key="index" :index="index" :focused="focusedTile" :file="file" />     
            </div>
            <button id="right" v-if="focusedTile < numTiles" @click="shiftRight">
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M5 3l3.057-3 11.943 12-11.943 12-3.057-3 9-9z"/></svg>
            </button>
      </div>      
  </div>
</template>

<script>
import tile from "./prev-tile.vue";

export default {
    name: "gallery",
    components: {
        tile
    },
    data(){
        return{
            files: [],
            focusedTile: 0,
            numTiles: 0
        }
    },
    created(){
        // console.log(this.$store.getters.data)
        this.files = this.$store.getters.data
    },
    mounted(){
        // console.log(document.getElementById("preview").children)
        this.numTiles = document.getElementById("preview").children.length - 1;
    },
    methods: {
        shiftLeft(){
            if(this.focusedTile > 0){
                this.focusedTile--;
            }
        },
        shiftRight(){
            if(this.focusedTile < document.getElementById("preview").children.length - 1){
                this.focusedTile++;
            }
        }
    }
}
</script>

<style scoped>
    #gallery{
        max-width: 100vw;
        /* height: 100vh; */
        position: relative;
        display: flex;
        align-items: center;
        flex-flow: column;
    }
    #prev-container{
        position: relative;
        /* background-color: aqua; */
        width: 65%;
        height: 200px;
        overflow: hidden;
    }
    #preview{
        display: flex;
        flex-flow: row;
        /* justify-content: center; */
        width: 100%;
        height: 200px;
        align-items: center;
        /* background-color: green; */
    }
    #left, #right{
        position: absolute;
        top: 0; bottom: 0%;
        margin: auto;
        background-color: rgba(169, 169, 169, 0.5); 
        z-index: 50;
        width: fit-content;
        height: fit-content;
        padding: 5px;
    }   
    #left{
        left: 0;
    }
    #right{
        right: 0;
    }
    #left:hover, #right:hover{
        background-color: rgba(169, 169, 169, 0.75); 
    }
    h1{
        font-size: x-large;
        font-weight: bolder;
        text-align: center;
        padding: 60px 0 20px 0;
    }
    p{
        text-align: center;
        margin: 10px;
    }
    #btn{
        background-color: aqua;
        padding: 5px;
        border-radius: 5px;
        box-shadow: 0 0 5px -3px #000;
    }

    @media screen and (max-width: 768px) {
        #prev-container{
            height: fit-content;
        }
        #preview{
            flex-flow: row wrap;
            height: fit-content;
            row-gap: 20px;
            justify-content: center;
            align-items: center;
        }
        #left, #right{
            display: none;
        }
    }
</style>