<template>
    <div id="background">
        <svg id="back-arrow" @click="$router.push({name: 'Gallery'})" width="24" height="24" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" clip-rule="evenodd"><path d="M21.883 12l-7.527 6.235.644.765 9-7.521-9-7.479-.645.764 7.529 6.236h-21.884v1h21.883z"/></svg>
        <div id="body">
            <h2>Login</h2>
            <span id="error">{{errMessage}}</span>
            
            <div class="container">
                <input type="text" v-model="username" ref="username" :class="{active: username != ''}" @keyup.enter="tab">
                <label for="">Username</label>
            </div>
            
            <div class="container">
                <input type="text" v-model="password" ref="password" :class="{active: password != ''}" @keyup.enter="submit">
                <label for="">Password</label>
            </div>
            
            <button @click="login" :disabled="isDisabled">Login</button>
        </div>
    </div>
</template>

<script>
import axios from "axios"

export default {
    name: "Login",
    data(){
        return{
            username: "",
            password: "",
            errMessage: ""
        }
    },
    mounted(){
        this.$refs.username.focus();
    },
    computed: {
        isDisabled(){
            if(this.username == "" || this.password == ""){
                return true
            }
            else return false
        }
    },
    methods: {
        tab(){
            this.$refs.password.focus();
        },
        async login(){
            if(this.username != "" && this.password != ""){
                this.errMessage = ""
                await axios.post("/api/login", {
                    username: this.username,
                    password: this.password
                }).then((res) => {
                    console.log(res)
                    window.sessionStorage.setItem("loggedIn", "true")
                    this.$router.push({name: "Gallery"})
                }).catch((err) => {
                    this.errMessage = err.response.data;
                })
            }
        }
    }
}
</script>

<style scoped>
    #background{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100vw;
        height: 100vh;
        background-color: #9992;
    }
    #body {
        width: 35%;
        height: fit-content;
        background-color: #fff;
        border-radius: 15px;
        box-shadow: 0 0 10px -5px #000a;
        text-align: center;
        display: flex;
        flex-flow: column;
        align-items: center;
        padding: 15px 0 15px 0;
    }
    #back-arrow{
        position: absolute;
        top: 50px;
        left: 50px;
        transform: rotateY(180deg) scale(1.5);
    }
    #back-arrow:hover{
        cursor: pointer;
        fill: #777;
    }
    h2{
        font-size: x-large;
        font-weight: 600;
        color: #0249ad;
    }
    #error{
        height: 16px;
        color: #aa0000;
        font-style: italic;
    }
    .container{
        position: relative;
        width: fit-content;
        padding: 20px;
    }
    .container>input{
        position: relative;
        font-size: 16px;
        padding: 2px 5px 2px 5px;
        background-color: #fff0;
        border-bottom: 2px solid #0249ad83;
        z-index: 1;
    }
    .container>label{
        position: absolute;
        left: 30px;
        bottom: 25px;
        margin: auto;
        user-select: none;
    }
    input:focus, input.active{
        outline: none;
        border-bottom: 2px solid #0249ad;
    }
    input:focus ~ label, input.active ~ label{
        animation: textShift 0.25s linear;
        font-size: 12px;
        bottom: 45px;
        left: 20px;
    }

    button{
        background-color: #0249ad;
        padding: 10px;
        border-radius: 5px;
        color: #fff;
        border: 1px solid #0257ce;
        box-shadow: 0 6px #003d92;
        margin-top: 25px;
    }
    button:disabled{
        opacity: 0.5;
    }
    button:hover{
        cursor: pointer;
        background-color: #003e94;
        box-shadow: 0 6px #00337a;
    }
    button:focus{
        background-color: #003d92;
        box-shadow: 0 6px #0249ad;
    }

    @keyframes textShift {
        from{
            left: 30px;
            bottom: 25px;
            font-size: 16px;
        } to {
            bottom: 45px;
            left: 20px;
            font-size: 12px;
        }
    }
</style>