<template>
  <div class="tile" @click="link">
      <div class="tile-body">
          <span class="title">{{file.title}}</span>
            <!-- {{file}} -->

            <div class="description">
                {{file.description}}
            </div>
      </div>
  </div>
</template>

<script>
export default {
    name: "tile",
    props: [
        'file',
        'index',
        'focused'
    ],
    data(){
        return{
            screenSize: 0
        }
    },
    created(){
        this.screenSize = screen.width;
        // console.log(this.screenSize)
    },
    mounted(){
        if(this.screenSize > 768){
            this.updateDiv()
            this.$el.style.left = (this.$props.index - this.$props.focused) * this.$el.offsetWidth + "px"
        }
    },
    updated(){
        // console.log((this.$props.index - this.$props.focused) * 100);
        // console.log(this.$el.style)
        if(this.screenSize > 768){
            if(this.$props.index == this.$props.focused){
                this.$el.style.zIndex = 100;
            } else {
                this.$el.style.zIndex = 0;
            }
            this.updateDiv()
            this.$el.style.left = (this.$props.index - this.$props.focused) * this.$el.offsetWidth + "px"
        }  
    },
    methods: {
        link(){
            console.log("Clicked")
        },
        updateDiv(){
            if(this.$props.index == this.$props.focused){
                this.$el.classList.add("focused");
                this.$el.style.width = 175 + "px";
                this.$el.style.height = 175 + "px";
            } else {
                this.$el.classList.remove("focused");
                // console.log(this.$el)
                // console.log(10 - Math.abs(this.$props.index - this.$props.focused))
                this.$el.style.zIndex = 10 - Math.abs(this.$props.index - this.$props.focused)
                // console.log(this.$el)
                this.$el.style.width = 175 - Math.abs(this.$props.index - this.$props.focused)*15 + "px";
                this.$el.style.height = 175 - Math.abs(this.$props.index - this.$props.focused)*15 + "px";
                // console.log(["width", 175 - Math.abs(this.$props.index - this.$props.focused)*25])
            }
        }
    }
}
</script>

<style scoped>
    .tile{
        position: absolute;
        margin: auto;
        left: 0; right: 0;
        width: 10px;
        height: 10px;
        border-radius: 15px;
        box-shadow: 0 0 5px -3px #000;
        background-color: #fff;
    }
    .focused{
        width: 175px;
        height: 175px;
        background-color: coral;
        z-index: 50;
    }
    .tile-body{
        width: 100%;
        height: 100%;
        position: relative;
        overflow: hidden;
    }

    .description{
        height: 0;
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: blue;
        top: -100%;
        transition: top 0.5s linear;
        border-radius: 15px;
        padding: 10px;
    }
    .focused .tile-body:hover{
        cursor: pointer;
    }
    .focused .tile-body:hover > .description{
        height: fit-content;
        height: 100%;
        top: 0;
        transition: top 0.5s linear;
    }

    @media screen and (max-width: 768px) {
        .tile{
            background-color: green;
            position: relative;
            width: 200px;
            height: 200px;
        }
    }
</style>