<template>
    <div :class="{opened: isActive}" id="home" @scroll.passive="navUpdate">
        <nav :class="{scrolled: isNotTop, open: isActive}">
            <a href="#intro" @click="isActive = false" class="logo">Logan Reuter</a>

            <!-- <router-link to="/login" class="link">Home</router-link> -->
            <div id="ham-menu" :class="{active: isActive}" @click="isActive = !isActive">
              <div class="line"></div>
              <div class="line"></div>
              <div class="line"></div>
            </div>

            <a href="#about-me" @click="isActive = false" class="link">About Me</a>
            <a href="#gallery" @click="isActive = false" class="link">Gallery</a>
            <a href="#contact-me" @click="isActive = false" class="link">Contact Me</a>
        </nav>

        <intro />
        <about />
        <gallery/>
        <contact />
    </div>
</template>

<script>
import intro from '../components/Home/intro.vue';
import about from '../components/Home/about-me.vue';
import gallery from "../components/Home/gallery-prev.vue";
import contact from "../components/Home/contact-me.vue";

export default {
    name: "Home",
    components: {
        intro,
        about,
        gallery,
        contact
    },
    data(){
      return{
        isNotTop: false,
        isActive: false
      }
    },
    mounted(){
      document.addEventListener('scroll', () => {
        if(document.getElementById("home").getBoundingClientRect().top < -40){
          this.isNotTop = true
        } else {
          this.isNotTop = false
        }
      })
    }
}
</script>

<style scoped>
nav{
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  /* background-color: #2c3e50; */
  padding: 10px;
  color: #000;
  text-align: right;
  z-index: 1000;
}
.logo{
  float: left;
}
.link{
  margin: 2px 10px 2px 10px;
  padding: 5px;
}
.link:hover{
  background-color: rgba(61, 60, 60, 0.527);
  color: #fff;
  border-radius: 5px;
}


.scrolled{
  background-color: #fff;
  box-shadow: 0 0 5px -3px #000;
}

#ham-menu{
  display: none;
  width: fit-content;
  position: absolute;
  top: 5px; right: 5px;
}

.line{
  width: 40px;
  border: 2px solid #000;
  margin: 5px;
  background-color: #000;
}

#ham-menu.active > .line:nth-child(1){
  /* animation: tiltDown 1s; */
  transform: translateY(9px) rotate(45deg);
}

#ham-menu.active > .line:nth-child(2){
  opacity: 0;
}

#ham-menu.active > .line:nth-child(3){
  transform: translateY(-9px) rotate(-45deg);
}

@media  screen and (max-width: 768px) {
  #ham-menu{
    display: block;
  }
  nav > .link{
    display: none;
  }

  nav.open > .link{
    display: inline-block;
    font-size: x-large;
    padding: 10px;
    font-weight: bold;
  }

  nav.open{
    height: 100vh;
    background-color: #fff;
    text-align: center;
    display: flex;
    flex-flow: column;
    row-gap: 10px;
  }
  nav.open ~ body{
    overflow: hidden;
    background-color: chocolate;
    height: 100%;
  }

  .opened{
    height: 100%;
    position: fixed;
    overflow: hidden;
  }
  .logo{
    text-align: left;
    /* align-self: left; */
  }
}


</style>